*{
  box-sizing: border-box;
}

.App {
  text-align: center;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

img{
  width: 100%;
}

button{
  border-style: solid;
  margin-top: 5px;

}
form{
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: auto;
  /* padding: 10%; */
  border:solid 5px aqua;
  padding-left: 5%;
}

input{
  height: 10px;
  flex: 0 0 20px;
}

label{
  margin: 5px 0 5px 0;
}

