/* Greeting Card component styling */

.gc-component-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.greeting-card-container{
  display: flex;
  width: 75%;
  border:solid 5px aqua;
  border-width: 3px;
  padding: 2%;
}

.card-image{
  /* display: flex; */
  width: 100%;
  border: solid 5px purple;
  margin: auto;

}

.card-message{
  border: solid 5px yellow;
  margin: auto;
  padding: 10%;
}

.reshare-message-wrapper{
  flex-wrap: wrap;
  border: solid 5px lightcoral;

}
.reshare-message{
  display: flex;
  flex-direction: column;
  border: solid 5px green;
}

/* Preview styling:
see Greeting Card component styling for greeting card styling*/

.button-controls{
  border: solid 5px rgb(2, 0, 128);
}

/* Card Form Styling: */
.card-form-container{
  display: flex;
  width: 70%;
  padding: 5%;
  border: solid 5px rgb(2, 0, 128);
}

@media screen and (max-width: 768px ) {
  .greeting-card-container{
    flex-direction: column;
    align-items: center;
  }
  .card-form-container{
    flex-direction: column;
    align-items: center;
  }
  
}